import React, { useState } from "react";
import { Link } from "react-router-dom";
import mapper from "../mapper";

const Sidebar = () => {


    // Recursive function to render nested lists with dynamic URLs
    const renderNestedList = (content, basePath = "") => {
        return (
            <ul>
                {Object.keys(content).map((key) => {
                    // Skip keys starting with `_` (like ___urlPath___)
                    if (key.startsWith("_")) return null;

                    // If the value is an object, render it recursively
                    if (typeof content[key] === "object") {
                        const newBasePath = `${basePath}/${content[key].___urlPath___}`;
                        return (
                            <li key={key}>
                                <span style={{ fontWeight: "bold" }}>{key}</span>
                                {renderNestedList(content[key], newBasePath)}
                            </li>
                        );
                    } else {
                        // If the value is not an object, render it as a link
                        return (
                            <li key={key}>
                                <Link to={`${basePath}/${key}`}>{key}</Link>
                            </li>
                        );
                    }
                })}
            </ul>
        );
    };

    return (
        <div>


            <div>
                <h2>Contents</h2>
                {renderNestedList(mapper.Contents, `/${mapper.Contents.___urlPath___}`)}
            </div>

        </div>
    );
};

export default Sidebar;
